import React, { Component } from 'react';
import vocabularyData from '../../data/vocabulary.json';

class Main extends Component {
    render() {

        const vocabulary = vocabularyData.map((data, index) => {
            return  <tr>
                        <td> {data.present} </td>
                        <td> {data.bnMeaning} </td>
                        <td> {data.past} </td>
                        <td> {data.pastParticiple} </td>
                    </tr>
           
        })

        return (
            <div className="container mt-5">
                <h2 className="text-center"> Verbs in Daily Uses </h2>
                <div className="table-responsive">
                    <table className="table table-sm table-hover text-center">
                        <thead>
                            <tr>
                            <th scope="col">Present</th>
                            <th scope="col">Bangla Meaning</th>
                            <th scope="col">Past</th>
                            <th scope="col">Past Participle</th>
                            </tr>
                        </thead>
                        <tbody>
                           {vocabulary}
                        </tbody>
                    </table>    
                </div>                
            </div>
        );
    }
}

export default Main;